import { Component, OnInit, ViewChild, ElementRef } from '@angular/core';
import { Chart } from 'chart.js';
import { skillLogos } from "../skills.mock";

@Component({
	selector: 'app-skills',
	templateUrl: './skills.component.html',
	styleUrls: ['./skills.component.scss']
})
export class SkillsComponent implements OnInit {

	
	@ViewChild('skills') canvas : ElementRef;
	public context: CanvasRenderingContext2D;
	public chart: any;
	
	public ctx: any
	public data: any[]

	constructor() { }

	ngOnInit() 
	{

	}

	ngAfterViewInit()
	{
		let dataSet: any[] = 
		[
			{
				data:
				[
					{
						x: -8,
						y: 8,
						r: 0
					},
					{
						x: 8,
						y: -8,
						r: 0
					},
				],
			},
		]

		skillLogos.forEach((logo,index,skills) => 
		{				
			let tmpImage = new Image()
			tmpImage.src = logo.src
			
			let ratio = this.calculateAspectRatioFit(tmpImage.width,tmpImage.height,100,80)
			tmpImage.height = ratio.height
			tmpImage.width = ratio.width

			let point = 
			{
				data:
				[
					{
						x: logo.x,
						y: logo.y,
						r: 5
					},					
				],
				pointStyle: tmpImage
			}

			dataSet.push(point)
		});
		
		this.context = (<HTMLCanvasElement>this.canvas.nativeElement).getContext('2d');

		this.chart = new Chart(this.context,
		{
			type: 'bubble',
			data:
			{
				datasets: dataSet					
			},				
			options:
			{
				legend:
				{
					display: false
				},
				scales:
				{
					xAxes:
					[
						{
							display: false
						}
					],
					yAxes: 
					[
						{
							display: false
						}
					],
				}
			}
		});		
	}

	 /**
	 * Conserve aspect ratio of the original region. Useful when shrinking/enlarging
	 * images to fit into a certain area.
	 *
	 * @param {Number} srcWidth width of source image
	 * @param {Number} srcHeight height of source image
	 * @param {Number} maxWidth maximum available width
	 * @param {Number} maxHeight maximum available height
	 * @return {Object} { width, height }
	 */
	calculateAspectRatioFit(srcWidth, srcHeight, maxWidth, maxHeight) 
	{
		var ratio = Math.min(maxWidth / srcWidth, maxHeight / srcHeight);

		return { width: srcWidth*ratio, height: srcHeight*ratio };
	}

}
